import React from "react";
import { ServiceItem } from "../../HomeTwo/Services";

const OurService = () => {
  return (
    <>
      <div className="tp-service-ara grey-bg-4 pt-140 pb-110">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <div className="tp-section-wrapper text-center mb-70">
                <span className="tp-section-subtitle mb-30">OUR SERVICES</span>
                <h2 className="tp-section-title">Explore Our Services</h2>
              </div>
            </div>
          </div>
          <div className="row">
            <ServiceItem
              icon="pe-7s-car"
              title="Road Freight"
              desc="Reliable and cost-effective transportation solutions for goods across cities and states."
            />
            <ServiceItem
              icon="pe-7s-paper-plane"
              title="Air Freight"
              desc="Fast and efficient air cargo solutions to ensure timely delivery of your shipments worldwide."
            />
            <ServiceItem
              icon="pe-7s-shopbag"
              title="Buyer-Seller Connections"
              desc="Helping buyers and sellers of fresh fruits & vegetables connect seamlessly for trade."
            />
            <ServiceItem
              icon="pe-7s-home"
              title="Warehousing & Storage"
              desc="Secure warehousing solutions to store and manage inventory efficiently."
            />
          </div>
        </div>
      </div>
    </>
  );
};

export default OurService;
