import React from "react";
import { Link } from "react-router-dom";
import MobileMenu from "./MobileMenu";
import MobileMenus from "./MobileMenus";

const OffCanvas = ({ setOffCanvas, isOffCanvas }) => {
  return (
    <>
      <div
        className={`offcanvas__area off-canvas-bg ${isOffCanvas ? "opened" : ""
          }`}
        style={{ backgroundImage: `url("assets/img//bg/Sidearea-bg-img.png")` }}
      >
        <div className="offcanvas_area-logo">
          <span className="offcanvas__close-btn">
            <button onClick={() => setOffCanvas(false)}>
              <i className="fal fa-times" />
            </button>
          </span>
        </div>
        <div className="offcanvas-content">
          <div className="offcanvas-subtitle">
            <span>ELEVATE YOUR BUSINESS WITH</span>
          </div>
          <div className="offcanvas-logo pb-30 pt-30">
            <img width={197} height={10} src="assets/img/logo/logo.png" alt="" />
          </div>
          <div className="offcanva-details">
            <p>
              We ensure seamless logistics and freight solutions to keep your business moving. With a focus on efficiency and reliability, we help you connect, transport, and grow effortlessly.
            </p>
          </div>
        </div>

        <div className="tp-mobile-menu mean-container d-block d-xl-none">
          <div className="mean-bar">
            <MobileMenus />
          </div>
        </div>

        <div className="tp-footer__widget pb-30 offcanvas-ct-info">
          <h3 className="tp-footer__widget-title">Quick Links</h3>
          <ul>
            <li><a href="#">50 /1 shivaji chowk Mulund colony  MULUND WEST MUMBAI 400082 </a></li>
            <li><a href="tel:+918879644445">(+91)8879644445</a></li>
            <li><a href="mailto:inf@jkfreight.com">inf@jkfreight.com</a></li>
            <li><span> Office Hours: 9AM - 5PM</span></li>
            <li><span> Monday - Friday</span></li>
          </ul>
        </div>
      </div>
      <div
        onClick={() => setOffCanvas(false)}
        className={`body-overlay ${isOffCanvas ? "opened" : ""}`}
      ></div>
    </>
  );
};

export default OffCanvas;
